import wx from 'weixin-js-sdk';
import MtaH5 from 'mta-h5-analysis';
import { getJSSDKSign } from './api';
import { PAGE_BASE_URL, LANG } from './config';
import { data as metaData } from './resource';

const { meta } = metaData[LANG];
export function setShare({
  title = meta.title, // 
  desc = meta.desc,
  link = `${PAGE_BASE_URL}/index.html`, // 
  imgUrl = `${PAGE_BASE_URL}/share.png`, // 
}) {
  wx.onMenuShareAppMessage({
    title,
    link: `${link}?CKTAG=mta_share.wechat_friend`,
    imgUrl,
    desc,
    success: () => {
      MtaH5.clickShare('wechat_friend'); // h5
    },
  });

  wx.onMenuShareTimeline({
    title,
    link: `${link}?CKTAG=mta_share.wechat_moments`,
    imgUrl,
    desc,
    success: () => {
      MtaH5.clickShare('wechat_moments'); // h5
    },
  });
}

export async function initJsSDK() {
  try {
    const data = await getJSSDKSign();
    const {
      appId, timestamp, nonceStr, signature,
    } = data;
    wx.config({
      debug: false, // 
      appId, // 
      timestamp, // 
      nonceStr, // 
      signature, // 1
      jsApiList: [
        // JS2
        'onMenuShareTimeline',
        'onMenuShareAppMessage',
        'onMenuShareQQ',
        'onMenuShareWeibo',
        'onMenuShareQZone',
      ],
    });

    wx.ready(() => {
      setShare({});
    });

    wx.error((res) => {
      console.log('wx.error: ', res);
    });
  } catch (e) {
    console.log(e);
  }
}
